import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
//Route Pages
import Home from './component/Home.vue'
import Project from './component/Project.vue'
import Blog from './component/Blog.vue'
import Resume from './component/Resume.vue'
//Page Components
import BackToTop from './component/BackToTop.vue'
import PostPage from './component/PostPage.vue'
import Footer from './component/Footer.vue'

require('./less/style.less')

Vue.use(VueRouter)
//define routes
const routes = [
  { path: '/', component: Home },
  { path: '/home', component: Home },
  { path: '/project', component: Project },
  { path: '/blog/:postName', component: PostPage },
  { path: '/blog', component: Blog },
  { path: '/resume', component: Resume }
]

const router = new VueRouter({
  routes,
  mode: 'history'
})

//define components
Vue.component('back-to-top', BackToTop)
Vue.component('page-footer', Footer)
Vue.nextTick(function () {
  MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
})
//instatinat the vue instance
var vm = new Vue({
  //define the selector for the root component
  el: '#app',
  //pass the template to the root component
  template: '<App/>',
  //declare components that the root component can access
  components: { App },
  //pass in the router to the Vue instance
  router
}).$mount('#app')//mount the router on the app

//console.log(this.$refs);



