<template>
  <div id="categoryCard" class="blog-container">
    <div class="blog-header"></div>
    <div class="category-body">
      <div class="category-title"><span>Categories</span></div>
      <div class="category-summary">
        <div class="category-section" v-for="subject in category['index']">
          <span class="category-expand fa fa-plus"></span>
          <span class="category-expand">{{category[subject].name}}</span>
          <div class="category-content category-hide" v-for="entry in category[subject].content">
            <span class="fa fa-circle"></span>
            <span><router-link v-bind:to="{path: entry.link}" append>{{entry.title}}</router-link></span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'categoryCard',
    props: ['category'],
    created: function() {
      $(function() {
        $('.category-expand').click(function() {
          $(this).siblings('.category-content').toggleClass('category-hide');
        });
      })
    }
  }
</script>



