<template>
  <div id="blogCard" class="blog-container">
    <div class="blog-header"></div>
    <div class="blog-body">
      <div class="blog-tag"><a href="javascript:void(0)">{{post.tag}}</a></div>
      <div class="blog-title"><router-link v-bind:to="{path: post.link}" append>{{post.title}}</router-link></div>
      <div class="blog-date">{{post.date}}</div>
      <div class="blog-summary">
        {{post.summary}}
        <div class="blog-footer"><router-link v-bind:to="{path: post.link}" append>Read more</router-link></div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'blogCard',
    props: ['post']
  }
</script>



