<template>
    <button id="btnBackToTop" type="button"></button>
</template>

<script>
  export default {
    name: 'btnBackToTop',
    created: function() {
      $(function() {
        $(window).scroll(function() {
          if ( $(window).scrollTop() > 200 ) {
            $('#btnBackToTop').show();
          } else {
            $('#btnBackToTop').hide();
          }
        })

        $('#btnBackToTop').click(function() {
          $('html, body').animate({
            scrollTop: 0
          }, 400);
          return false;
        })
      })
    }
  }
</script>



