<template>
  <div id="app">
    <!--Navigation Bar-->
    <div class="navbar"
         v-bind:class="{'navbar-center': $route.path === '/home' || $route.path === '/'}">
      <ul>
        <li>
          <router-link v-bind:to="'/home'"><span>HOME</span></router-link>
          <a id="toggle-nav" href="javascript:void(0);"><span>&#9776;</span></a>
        </li>
        <li><router-link v-bind:class="{'active': $route.path === '/project'}" v-bind:to="'/project'"><span>PROJECTS</span></router-link></li>
        <li><router-link v-bind:class="{'active': $route.path === '/blog'}" v-bind:to="'/blog'"><span>BLOG</span></router-link></li>
        <li><router-link v-bind:class="{'active': $route.path === '/resume'}" v-bind:to="'/resume'"><span>RESUME</span></router-link></li>
      </ul>
      <transition name="fade" mode="out-in" appear>
        <router-view v-if="$route.path === '/home' || $route.path === '/'"></router-view>
      </transition>
    </div>
    <div class="page-container" v-if="$route.path != '/home' && $route.path != '/'">
      <transition name="fade" mode="out-in" appear>
        <router-view v-if="!($route.path === '/home' || $route.path === '/')"></router-view>
      </transition>
      <hr>
      <page-footer></page-footer>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'app',
    data() {
      return {
        isMobile: $(window).width() < 551,
        width: $(window).width()
      }
    },
    created: function() {
      $(function() {
        $('#toggle-nav').click(function() {
          if(!$('.navbar').hasClass('nav-menu')) {
            $('.navbar').addClass('nav-menu');
          } else {
            $('.navbar').removeClass('nav-menu');
          }
        });
      });
    }
  }
</script>

<style>
  .fade-enter-active {
    animation-name: fadeInDown;
    animation-duration: 1.5s;
  }

  .fade-leave-active {
    animation-name: fadeOutDown;
    animation-duration: 0s;
  }

  @keyframes fadeInDown {
    from { opacity: 0.05;}
    to { opacity: 1;}
  }

  @keyframes fadeOutDown {
    from {  opacity: 1;}
    to { opacity: 0.05;}
  }
</style>



