<template>
  <div id="postPage">
    <div class="page-header">{{postTitle}}</div>
    <hr>
    <div class="page-content" v-html="postContent">
    </div>
    <back-to-top></back-to-top>
  </div>
</template>
<script>
  import Article from '../asset/article.json'

  export default {
    name: 'postPage',
    created: function() {
      this.$nextTick(function() {
				 MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
		  });
    },
    data() {
      return {
        page: this.$route.params.postName,
        postTitle: Article[this.$route.params.postName]['title']
      }
    },
    computed: {
      postContent() {
        return require('../asset/blog-posts/'+this.page+'.html');
      }
    }
  }
</script>



