<template>
  <div id="blog">
    <div class="page-header">Writings & Thoughts</div>
    <hr>
    <div class="page-content">
      <div class="blogMain">
        <blog-card
          v-for="article in blogPosts"
          v-bind:post="article"
          :key="article.link">
        </blog-card>
      </div>
      <div class="blogSide">
        <category-card v-bind:category="blogCategory"></category-card>
      </div>
    </div>
    <back-to-top></back-to-top>
  </div>
</template>

<script>
  import BlogCard from './BlogCard.vue'
  import CategoryCard from './CategoryCard.vue'

  import Posts from '../asset/post.json'
  import Category from '../asset/category.json'

  export default {
    name: 'blog',
    data() {
      return {
          blogPosts: Posts.contents,
          blogCategory: Category
      }
    },
    components: {
      'blog-card': BlogCard,
      'category-card': CategoryCard
    }
  }
</script>



